.luxury{
    width: 691px;
    height: 372px;
    margin: 20px auto 100px;
    padding: 39px 0 0 0;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    &__background-div{
        position: absolute;
        bottom: 0px;
        z-index: -3;
    }
}

.luxury-div__skip{
    margin: 66px 0 0 31px;
}

.luxury-div__text{
    height: 234px;
    width: 700px;
    margin: 0 0 0 30px;
    position: relative;
    &__svg-social{
        position: absolute;
        width: 14px;
        height: 109px;
        left: 665px;
        top: 20px;
    }
    &__svg-768{
        position: absolute;
        width: 74px;
        height: 234px;
        background-color: transparent;
        fill: transparent;
    }
    &__svg2{
        position: absolute;
        width: 10px;
        height: 14.13px;
        bottom: 27px;
    }
    &__p{
        margin: 34px 0 7px 25px;
    }
    &__p2{
        font-weight: 700;
        font-size: 16px;
        line-height: 18.77px;
        width: 158px;
        height: 19px;
        margin-left: 25px;
    }
    &__h1{
        font-weight: 700;
        font-size: 50px;
        line-height: 63.8px;
        width: 400;
        height: 128px;
        margin: 0 0 8px 25px;
        &__span{
            width: 60px;
        }
    }
}